body{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  background-color: rgb(239, 172, 224);
}
button{
  color: purple;
  font-weight: bold;
  font-size: 25px;
  border: 2px solid rgb(159, 26, 159);
}
.row{
  margin: 0px 0px;
  padding: 0px 0px;
  width: 50vw;
  height: 50vh;
}
#drum-machine{
  width: 50vw;
  height: 50vh;
  display: flex;
  align-items: center;
  flex-direction: column; 
}
#display{
  font-size: 30px;
  width: 40px;
  background-color: purple;
  color: rgb(239, 172, 224);
  margin-bottom: 20px;
}